<template>
    <div class="identify-box">
        <div class="container">
            <div class="header">
                <img src="../assets/face.9f8714c (1).png" class="bg" ref="facebg">
                <div class="facerec_animate">
                    <div class="cycle_animate" ref="chuxas">
                        <div class="cycle">
                            <img src="../assets/fan2ge.png" class="huan1">
                        </div>
                        <div class="cycle">
                            <img src="../assets/fang1ge.png" class="huan2">
                        </div>
                        <div class="cycle">
                            <img src="../assets/fan2ge.png" class="huan3">
                        </div>
                    </div>
                     <!-- <div  class="cycle">
                        <img src="../assets/face.9f8714c (1).png"   class="sal" ref="salface">
                    </div> -->
                    <!-- <canvas id="facerec" width="411" height="197" class="facerec"></canvas> -->
                </div>
            </div>
            <div class="segment">
                <div class="item">
                    <div class="itemtitle znei">测量三庭长度</div>
                    <div class="itemline"> </div>
                </div>
                <div class="item">
                    <div class="itemtitle">定位五官</div>
                    <div class="itemline"></div>
                </div>
                <div class="item">
                    <div class="itemtitle">综合分析</div>
                    <div class="itemline"></div>
                </div>
            </div>
            <div class="facerec_progress">
                <!-- swiper :options="swiperOption"  -->
                <swiper class="vux-swiper">
                    <swiper-slide  class="item">
                        <div class="itemcontent">
                            <div class="progressitem">
                                <div class="progressname">上庭数据提取</div>
                                <img src="../assets/kgjia.png" class="icon jiazas" ref="xuzas">
                            </div>
                            <div class="progressitem">
                                <div class="progressname">中庭数据提取</div>
                                <img src="../assets/kgjia.png" class="icon" ref="xuzas">
                            </div>
                            <div class="progressitem">
                                <div class="progressname">下庭数据提取</div>
                                <img src="../assets/kgjia.png" class="icon" ref="xuzas">
                            </div>
                        </div>
                    </swiper-slide>
                    <swiper-slide class="item">
                        <div class="itemcontent">
                            <div class="progressitem">
                                <div class="progressname">定位眉头-眉峰-眉尾</div>
                                <img src="../assets/kgjia.png" class="icon" ref="xuzas">
                            </div>
                            <div class="progressitem">
                                <div class="progressname">定位眼长-眼高</div>
                                <img src="../assets/kgjia.png" class="icon" ref="xuzas">
                            </div>
                            <div class="progressitem">
                                <div class="progressname">定位鼻高-鼻翼宽</div>
                                <img src="../assets/kgjia.png" class="icon" ref="xuzas">
                            </div>
                            <div class="progressitem">
                                <div class="progressname">定位嘴宽-嘴唇厚度</div>
                                <img src="../assets/kgjia.png" class="icon" ref="xuzas">
                            </div>
                        </div>
                    </swiper-slide>
                    <swiper-slide class="item">
                        <div class="itemcontent">
                            <div class="progressitem">
                                <div class="progressname">量取下颌角度</div>
                                <img src="../assets/kgjia.png" class="icon" ref="xuzas">
                            </div>
                            <div class="progressitem">
                                <div class="progressname">取各部位数据</div>
                                <img src="../assets/kgjia.png" class="icon" ref="xuzas">
                            </div>
                            <div class="progressitem">
                                <div class="progressname">匹配五行格局</div>
                                <img src="../assets/kgjia.png" class="icon" ref="xuzas">
                            </div>
                        </div>
                    </swiper-slide> 
                </swiper> 
                <div class="vux-indicator vux-indicator-right">
                    <a href="javascript:">
                        <i class="vux-icon-dot"></i>
                    </a>
                    <a href="javascript:">
                        <i class="vux-icon-dot active"></i>
                    </a>
                    <a href="javascript:">
                        <i class="vux-icon-dot"></i>
                    </a>
                </div>
            </div>
        </div> 
    </div>
</template>
<script>
import {swiper,swiperSlide} from 'vue-awesome-swiper'
import { setTimeout, setInterval } from 'timers';
export default {
    created(){
        console.log( this,'创建钱');
    },
    mounted(){
        setTimeout(()=>{
             this.$refs.facebg.style.width="4.3rem";
              setTimeout(()=>{
                  this.$refs.facebg.style.top="1.2rem";
                     setTimeout(()=>{
                         this.$refs.chuxas.style.display="block";
                     },1000)
            },800)
        },1000)
       
    
       
    },
    components:{
        swiper,
        swiperSlide
    }
}
</script>




